import { h } from 'vue'
import { reactive, computed, ref } from 'vue'
import { default as axios } from 'axios'
import store from 'store'
import { isFunction } from 'utils/is'
import { deepClone } from 'utils/deep-clone'
import { ElMessage } from 'element-plus'

const useAll = () => {
	const state = computed(() => store.state.integration.all)
	const stateEnabledIntegration = computed(() => store.state.integration.enabled_integration)
	const loading = reactive({
		all: false,
		update: false
	})
	const actionAll = async (callback = () => {}) => {
		try {
			loading.all = true
			const response = await store.dispatch('integration/all')
			isFunction(callback) && callback(response)
		} finally {
			loading.all = false
		}
	}
	const actionEnabledIntegration = async (callback = () => {}) => {
		try {
			loading.update = true
			const response = await store.dispatch('integration/enabledIntegration')
			isFunction(callback) && callback(response)
		} finally {
			loading.update = false
		}
	}
	const actionUpdate = async (integrationId, data, callback = () => {}, fallback = () => {}) => {
		const payload = Object.assign(
			{},
			{
				id: integrationId,
				data: data
			}
		)
		try {
			loading.update = true
			const response = await store.dispatch('integration/update', payload)
			isFunction(callback) && callback(response)
		} catch (exception) {
			isFunction(fallback) && fallback(exception)
		} finally {
			loading.update = false
		}
	}
	const getIntegrationByName = name => {
		return state.value.find(integration => integration.name.toLowerCase() === name.toLowerCase())
	}
	const enableIntegrationByName = name => {
		const index  = state.value.findIndex(integration => integration.name.toLowerCase() === name.toLowerCase())

		if (index === -1) return

		store.commit('integration/SET_ENABLE_INTEGRATION', index)
	}

	return {
		state,
		stateEnabledIntegration,
		loading,
		actionAll,
		actionEnabledIntegration,
		actionUpdate,
		getIntegrationByName,
		enableIntegrationByName
	}
}

const useShowAll = () => {
	const { state: stateWidgetUrl } = useShowWidgetURL()
	const state = computed(() => store.state.integration.show.all)
	const refreshDatafeed = computed(() =>
		!stateWidgetUrl.value || !stateWidgetUrl.value.length
			? false
			: store.state.integration.refresh_datafeed
	)
	const statePublicApi = computed(() => store.state.integration.show.public_api)
	const stateAllDataFeed = computed(() => store.state.integration.show.datafeed.all)
	const stateShowDataFeed = computed(() => store.state.integration.show.datafeed.show)
	const data = reactive({})
	const loading = reactive({
		all: false,
		update: false,
		public_api: false,
		data_feed: false
	})

	const datafeed = reactive({
		page: 1,
		limit: 10
	})

	const actionAll = async (callback = () => {}) => {
		try {
			loading.all = true
			await store.dispatch('integration/showAll')
			Object.assign(data, deepClone(state.value))
			isFunction(callback) && callback()
		} finally {
			loading.all = false
		}
	}
	const actionUpdate = async (data, callback = () => {}, fallback = () => {}) => {
		try {
			loading.update = true
			const response = await store.dispatch('integration/updateAll', data)
			isFunction(callback) && callback(response)
		} catch (exception) {
			isFunction(fallback) && fallback()
		} finally {
			loading.update = false
		}
	}
	const actionPublicApi = async (callback = () => {}) => {
		try {
			loading.public_api = true
			await store.dispatch('integration/showPublicApi')
			Object.assign(data, statePublicApi.value.data)
			isFunction(callback) && callback()
		} finally {
			loading.public_api = false
		}
	}
	const actionGeneratePublicApiKey = async (callback = () => {}) => {
		try {
			loading.public_api = true
			const response = await store.dispatch('integration/showGeneratePublicApiKey')
			isFunction(callback) && callback(response)
		} finally {
			loading.public_api = false
		}
	}
	const actionAllDataFeed = async (callback = () => {}) => {
		try {
			loading.data_feed = true
			await store.dispatch('integration/allDataFeed', datafeed)
			isFunction(callback) && callback()
		} finally {
			loading.data_feed = false
		}
	}
	const resetAllDataFeed = async (callback = () => {}) => {
		try {
			datafeed.page = 1
			loading.data_feed = true
			await store.dispatch('integration/allDataFeed', datafeed)
			isFunction(callback) && callback()
		} finally {
			loading.data_feed = false
		}
	}
	const actionShowDataFeed = async (callback = () => {}) => {
		try {
			loading.data_feed = true
			await store.dispatch('integration/showDataFeed')
			isFunction(callback) && callback()
		} finally {
			loading.data_feed = false
		}
	}
	const actionRefreshDataFeed = async payload => {
		await store.dispatch('integration/setRefreshDataFeed', payload)
	}
	return {
		state,
		stateWidgetUrl,
		stateAllDataFeed,
		stateShowDataFeed,
		data,
		datafeed,
		loading,
		actionAll,
		actionUpdate,
		actionPublicApi,
		actionGeneratePublicApiKey,
		actionAllDataFeed,
		actionShowDataFeed,
		refreshDatafeed,
		actionRefreshDataFeed,
		resetAllDataFeed
	}
}

const useTestConnection = () => {
	const loading = ref(false)
	const repairdesk = async (key, callback = () => {}) => {
		try {
			loading.value = true
			const response = await store.dispatch('integration/repairdesk/testConnection', key)
			isFunction(callback) && callback(response)
		} finally {
			loading.value = false
		}
	}
	const wholeCell = async (payload, callback = () => {}) => {
		try {
			loading.value = true
			await store.dispatch('integration/wholecell/testConnection', payload)
			ElMessage({
				type: 'success',
				message: 'WholeCell.io connection succeed!'
			})
		} catch (error) {
			ElMessage({
				type: 'warning',
				message: 'WholeCell.io connection failed!'
			})
		} finally {
			loading.value = false
		}
	}
	return {
		repairdesk,
		wholeCell,
		loading
	}
}

