const useShowUspsForm = () => {
	const data = reactive({
		user_id: null,
		// merchant_account_id: null,
		merchant_account_code: null,
		mid: null,
		is_default: 0,
		location: null,
		zip_code: null
	})
	const loading = ref(false)
	const setData = (payload = {}) => {
		data.user_id = payload.user_id || null
		// data.merchant_account_id = payload.merchant_account_id || null
		data.merchant_account_code = payload.merchant_account_code || null
		data.mid = payload.mid || null
		data.is_default = payload.is_default || 0
		data.location = payload.location || null
		data.zip_code = payload.zip_code || null
	}
	const actionShow = async (id, callback = () => {}) => {
		loading.show = true
		const response = await store.dispatch(`integration/showUsps`, id)
		setData(response)
		isFunction(callback) && callback()
		loading.show = false
	}
	const actionCreate = async (callback = () => {}) => {
		try {
			loading.value = true
			await store.dispatch(`integration/showUspsCreate`, data)
			isFunction(callback) && callback()
		} finally {
			loading.value = false
		}
	}
	const actionUpdate = async (id, callback = () => {}) => {
		try {
			loading.value = true
			await store.dispatch(`integration/showUspsUpdate`, {
				id,
				data
			})
			isFunction(callback) && callback()
		} finally {
			loading.value = false
		}
	}

	return {
		data,
		loading,
		actionShow,
		actionCreate,
		actionUpdate
	}
}

const useShowWidgetURL = () => {
	const state = computed(() => {
		const result = store.state.integration.data_feed_widget_url
		if (result.includes('https://')) {
			return result.replace('https://', '')
		}

		return result
	})
	const loading = ref(true)

	const actionGET = async () => {
		try {
			loading.value = true
			await store.dispatch('integration/showWidgetURLDatafeed')
		} finally {
			loading.value = false
		}
	}

	const actionPOST = async payload => {
		try {
			loading.value = true
			await store.dispatch('integration/updateWidgetURLDatafeeds', `https://${payload}`)
			ElMessage({
				type: 'info',
				message: h(
					'div',
					{
						class: 'buyback-custom-message--content'
					},
					[
						h('icon', { class: 'material-icons' }, 'downloading'),
						h('span', null, 'Feel free to navigate away or close this window â€” your Data Feed preparation will continue.')
					]
				),
				customClass: 'buyback-custom-message-class'
			})
		} finally {
			loading.value = false
		}
	}

	return {
		state,
		loading,
		actionGET,
		actionPOST
	}
}

const useRepairdesk = () => {
	const cancelToken = ref(null)
	const data = reactive({
		search: '',
		page: 1,
		lastPage: 1,
		limit: 10,
		apiKeys: reactive({}),
		items: [],
		filterFrom: undefined
	})

	const loading = ref(false)
	const loadingConfigs = computed(() => store.state.integration.repairdesk.loading.config)
	const configs = computed(() => store.state.integration.repairdesk.config)
	const actionShow = async (payload, callback = () => {}) => {
		if (cancelToken.value) cancelToken.value.cancel()
		cancelToken.value = axios.CancelToken.source()

		loading.value = true
		try {
			const response = await store.dispatch('integration/repairdesk/locations', payload)
			// data.page = !data.page && response.current_page
			// data.limit = response.per_page,
			data.lastPage = response.last_page
			data.items = response.data
			if (data.items.length) {
				data.items = data.items.map(item => {
					data.apiKeys[`apiKey${item.id}`] = item.repairdesk_api_key
					return {
						...item,
						is_edit: false
					}
				})
			}
			isFunction(callback) && callback()
		} finally {
			loading.value = false
		}
	}
	const actionUpdate = async (payload, callback = () => {}) => {
		loading.value = true
		try {
			await store.dispatch('integration/repairdesk/testConnection', {
				location_id: payload.id,
				repairdesk_api_key: payload.repairdesk_api_key
			})
			ElMessage({
				message: 'Connected!',
				type: 'success'
			})
			isFunction(callback) && callback()
		} finally {
			loading.value = false
		}
	}
	const actionDelete = async (id, callback = () => {}) => {
		loading.value = true
		try {
			await store.dispatch('integration/repairdesk/removeConnection', id)
			isFunction(callback) && callback()
		} finally {
			loading.value = false
		}
	}
	const updateConfig = async (callback = () => {}) => {
		try {
			store.commit('integration/repairdesk/SET_LOADING_CONFIG_UPDATE', true)
			await store.dispatch('integration/repairdesk/updateConfig')
			isFunction(callback) && callback(null)
		} catch (err) {
			isFunction(callback) && callback(err)
		} finally {
			store.commit('integration/repairdesk/SET_LOADING_CONFIG_UPDATE', false)
		}
	}
	const setConfig = config => store.commit('integration/repairdesk/SET_CONFIG', config)

	return {
		data,
		loading,
		configs,
		loadingConfigs,
		actionShow,
		actionUpdate,
		actionDelete,
		updateConfig,
		setConfig
	}
}

export {
	useAll,
	useShowAll,
	useShowFedex,
	useShowUsps,
	useShowUspsForm,
	useShowUps,
	useTestConnection,
	useShowWidgetURL,
	useRepairdesk
}
